<template>
  <div class="h-100b" style="padding-bottom: 3.571rem;overflow-y: auto;">
    <div style="padding: 1.071rem 1.071rem 0.714rem;">
      <van-image fix="cover" :src="helpfulDetail.project_cover" class="w-100b" height="11.429rem" />
      <div
        style="margin-top: 0.929rem;"
        class="font-14 color-282828"
      >{{helpfulDetail.project_title}}</div>
      <div class="font-10 color-989898 mg-top-15">{{helpfulDetail.project_content}}</div>

      <van-image
        fix="cover"
        src="https://p1.ifengimg.com/cmpp/2016/07/06/09/05620f3b-31c4-4db4-bf4e-11ec7f66ef1b_size21_w469_h240.jpg"
        class="w-100b mg-top-20"
        height="11.429rem"
      />
    </div>
    <p class="divider" style="height: 0.286rem;" />

    <!--捐款记录-->
    <div style="padding: 0.714rem 0.714rem 0.714rem 1.429rem;">
      <p class="color-282828 font-14">捐款记录</p>

      <van-cell
        class="color-989898 font-12 mg-top-15 dis-flex align-item-center"
        v-for="(item,index) of helpfulDetail.help_list"
        :key="index"
        style="padding: 0;"
      >
        <van-image
          fit="cover"
          round
          :src="item.userinfo.head_img"
          style="width: 2.071rem;height: 2.071rem;margin-right: 1.143rem;"
          slot="icon"
        />
        <div slot="title">{{item.userinfo.real_name}} 捐出{{item.helpful_price}}元</div>
        <div>{{item.create_time}}</div>
      </van-cell>
    </div>

    <van-button class="w-100b submit-bt donation-bt" color="#FEA503" size="large">立即捐款</van-button>
  </div>
</template>

<script>
import { helpdetails } from "../../api/api";
export default {
  name: "charitable-projects-detail",
  data() {
    return {
      helpfulDetail: {}
    };
  },
  created() {
    console.log(this.$route.query);
    let data = {
      helpful_id: this.$route.query.helpful_id
    };
    helpdetails(data).then(res => {
      console.log(res);
      this.helpfulDetail = res.data.data;
      console.log(this.helpfulDetail )
      let data = this.helpfulDetail.help_list;
      if (data) {
        for (let i in data) {
          let str = "";

          for (let j = 0; j < data[i].userinfo.real_name.length - 1; j++) {
            str += "*";
          }
          data[i].userinfo.real_name =
            data[i].userinfo.real_name.split("").splice(0, 1) + str;
            console.log(data[i].userinfo.real_name)
        }
        this.helpfulDetail.help_list=data
      }
    });
  }
};
</script>

<style scoped lang="less">
/*捐款按钮*/
.donation-bt {
  position: absolute;
  bottom: 0;
}
</style>
